<template>
  <div class="main">
    <div class="left">
      <div class="logo">
        <i>File Tracking</i>
      </div>
      <el-row style="margin-top: 25px">
        <el-col :span="24">
          <el-menu :default-active="$route.path" router>
            <el-menu-item class="left-nav" :class="{hide:authority[0].ishide}" index="/main/file-record">
              <i class="iconfont nav-icon icon-menulist"></i>{{language.nav_FileRecord}}</el-menu-item>
            <el-menu-item class="left-nav" :class="{hide:authority[1].ishide}" index="/main/file-make">
              <i class="iconfont nav-icon icon-menumaking"></i>{{language.nav_FileMake}}</el-menu-item>
            <el-menu-item class="left-nav" :class="{hide:authority[2].ishide}" index="/main/algorithm-manage">
              <i class="iconfont nav-icon icon-menualgorithm"></i>{{language.nav_AlgorithmManage}}</el-menu-item>
            <el-submenu index="1" :class="{hide:authority[3].ishide}">
              <template slot="title">
                <i class="iconfont nav-icon icon-menuview"></i>{{language.nav_Tracking}}
              </template>
              <el-menu-item class="left-nav" :class="{hide:authority[4].ishide}" index="/main/track-analysis">{{language.nav_TrackAnalysis}}</el-menu-item>
              <el-menu-item class="left-nav" :class="{hide:authority[5].ishide}" index="/main/track-detail">{{language.nav_TrackDetail}}</el-menu-item>
            </el-submenu>
            <el-submenu index="2" :class="{hide:authority[6].ishide}">
              <template slot="title">
                <i class="iconfont nav-icon icon-menufirewall"></i>
                {{language.nav_Firewall}}
              </template>
              <el-menu-item class="left-nav" :class="{hide:authority[7].ishide}" index="/main/monitor">{{language.nav_Monitor}}</el-menu-item>
              <el-menu-item class="left-nav" :class="{hide:authority[8].ishide}" index="/main/detailed">{{language.nav_Detailed}}</el-menu-item>
            </el-submenu>
          </el-menu>
        </el-col>
      </el-row>
    </div>
    <div class="right">
      <el-row class="right-menu">
        <el-col :span="24" class="clearfix">
          <div class="fr">
            <el-button class="right-set" @click="handleTurn('sc')">
              <i class="iconfont icon-systemcenter" style="margin-right:5px"></i>{{language.top_sc}}
            </el-button>
            <el-button class="right-set">
              <i class="iconfont icon-user" style="margin-right:5px"></i>{{userName}}
            </el-button>

          </div>
        </el-col>
      </el-row>
      <div class="container">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {
          nav_FileRecord: '',
          nav_FileMake: '',
          nav_AlgorithmManage: '',
          nav_Tracking: '',
          nav_TrackAnalysis: '',
          nav_TrackDetail: '',
          nav_Firewall: '',
          nav_Monitor: '',
          nav_Detailed: '',
          top_sc: ''
        },
        userName: '',
        authority: [{
            code: 'FileRecord',
            ishide: true
          },
          {
            code: 'FileMake',
            ishide: true
          },
          {
            code: 'AlgorithmManage',
            ishide: true
          },
          {
            code: 'Tracking',
            ishide: true
          },
          {
            code: 'TrackAnalysis',
            ishide: true
          },
          {
            code: 'TrackDetail',
            ishide: true
          },
          {
            code: 'Firewall',
            ishide: true
          },
          {
            code: 'Monitor',
            ishide: true
          },
          {
            code: 'Detailed',
            ishide: true
          }
        ],
      }
    },
    methods: {
      handleUser(command) {
        if (command == "1") {
          this.handleSignOut();
        }
      },
      handleSignOut() {
        const vm = this;
        vm.$http.get('/wyfx/user/logout')
          .then(function (response) {
            vm.$router.push({
              name: 'Login'
            });
          })
      },
      // 跳轉到其他系統
      handleTurn(url) {
        this.$http.get('/wyfx/page/redirect?path=' + url)
          .then(function (response) {
            window.location.href = response.data.msg;
          });
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      vm.$http.all([vm.$http.get('/wyfx/function/getMenuBySystem', {
            params: {
              code: 'FileTrace'
            }
          }),
          vm.$http.get('/wyfx/user/getLoginUser'),
          vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'Main'
            }
          })
        ])
        .then(vm.$http.spread(function (menu, user, language) {
          // 獲取菜单权限
          let data = menu.data.data;
          for (let i in data) {
            for (let j in vm.authority) {
              if (vm.authority[j].code == data[i].code) {
                vm.authority[j].ishide = false;
              }
            }
          }
          //获取用户信息
          vm.userName = user.data.msg;
          // 獲取文本信息
          vm.language = language.data.data;
          loading.close();
        }));
    }
  }

</script>

<style scoped>
  .main {
    height: 100%;
  }

  .left {
    width: 230px;
    height: 100%;
    background-color: #364760;
    color: #d9e1e5;
  }

  .right {
    margin-left: 230px;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
  }

  .logo {
    width: 100%;
    height: 60px;
    line-height: 60px;
    background-color: #2a394f;
    font-size: 24px;
    text-align: center;
  }

  .left-nav {
    background-color: #364760;
    color: #d9e1e5;
    height: 46px;
    line-height: 46px;
  }

  .left-nav:hover,
  .left-nav.is-active {
    background-color: #2a394f;
    color: #fff;
  }

  .right-menu {
    width: 100%;
    height: 60px;
    line-height: 60px;
    padding: 0 20px;
    border-bottom: 1px solid #d7dadd;
    background-color: #364760;
  }

  .iconfont {
    margin-right: 10px;
  }

  .el-dropdown-menu {
    border-color: #a5b2bf;
  }

  .right-set {
    border: none;
    background-color: #364760;
    color: #fff;
  }

  .right-set:hover {
    color: #15a4fa;
  }

  .container {
    width: 100%;
    position: absolute;
    top: 60px;
    bottom: 0;
    background-color: #e4e7ea;
  }

  .nav-icon {
    font-size: 24px;
    color: #15a4fa;
  }

  .hide {
    display: none;
  }

</style>



