<template>
  <div class="right-main">
    <!-- 选择文件弹出框 -->
    <el-dialog :title="language.choseFile" :visible.sync="dialogVisible" size="small" top="5%">
      <el-table :data="gridData" stripe>
        <el-table-column prop="uuid" label="ID">
        </el-table-column>
        <el-table-column prop="fileName" :label="language.tabFileName">
        </el-table-column>
        <el-table-column width="80">
          <template scope="scope">
            <el-button size="small" @click="handleChose(scope.$index, scope.row)">{{language.chose}}</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination layout="sizes, prev, pager, next, total" :current-page="currentPage" :page-size="pageSize" :page-sizes="[10, 20, 50, 100, 200]"
          :total="totalElements" @current-change="handleCurrentChange" @size-change="handleSizeChange"></el-pagination>
      </div>
    </el-dialog>
    <!-- 选择文件弹出框end -->
    <!-- 选择对比文件弹出框 -->
    <el-dialog :title="language.choseFile" :visible.sync="dialogDiffVisible" size="small" top="5%">
      <el-table :data="gridData" stripe>
        <el-table-column prop="uuid" label="ID">
        </el-table-column>
        <el-table-column prop="fileName" :label="language.tabFileName">
        </el-table-column>
        <el-table-column width="80">
          <template scope="scope">
            <el-button size="small" @click="handleDiffChose(scope.$index, scope.row)">
              {{language.chose}}</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination layout="sizes, prev, pager, next, total" :current-page="currentPage" :page-size="pageSize" :page-sizes="[10, 20, 50, 100, 200]"
          :total="totalElements" @current-change="handleCurrentChange" @size-change="handleSizeChange"></el-pagination>
      </div>
    </el-dialog>
    <!-- 选择对比文件弹出框end -->
    <el-row class="tool-bar">
      <el-col :span="24">
        <el-button @click="handleShowFile">{{language.choseFile}}</el-button>
        <el-date-picker @change="handleDataChange" v-model="dateRangeVal" type="daterange" :placeholder="language.drPH"></el-date-picker>
      </el-col>
    </el-row>
    <el-row class="info-bar">
      <el-col :span="9">
        <div>{{language.fileUid}}{{fileUid}}</div>
        <div class="info-mar">{{language.fileTag}}{{fileTag}}</div>
      </el-col>
      <el-col :span="9">
        <div>{{language.fileName}}{{fileName}}</div>
        <div class="info-mar">{{language.fileTime}}{{fileTime}}</div>
      </el-col>
      <el-col :span="6">
        <div class="file-views">{{language.fileViews}}
          <span style="font-size: 24px;">{{fileViews}}</span>
        </div>
      </el-col>
    </el-row>
    <el-row class="chart-row">
      <el-col :span="24">
        <div class="chart-card">
          <div id="map" class="chart" :class="{hide:chartHide}"></div>
          <el-radio-group v-model="mapJsonType" size="small" class="rankChart-radio" @change="handleMapChange" :class="{hide:chartHide}">
            <el-radio-button label="china.json">{{language.mapChartRadioChina}}</el-radio-button>
            <el-radio-button label="world.json">{{language.mapChartRadioWorld}}</el-radio-button>
          </el-radio-group>
          <div class="null-txt" :class="{hide:nullHide}">{{language.nullTxt}}</div>
        </div>
      </el-col>
    </el-row>
    <el-row class="chart-row" v-if="mapJsonType == 'china.json'">
      <el-col :span="24">
        <div class="chart-card">
          <div id="rankChart" class="chart" :class="{hide:chartHide}"></div>
          <el-radio-group v-model="rankChartRadio" size="small" class="rankChart-radio" @change="handleRankRadioChange" :class="{hide:chartHide}">
            <el-radio-button :label="1">{{language.rankChartRadioCity}}</el-radio-button>
            <el-radio-button :label="0">{{language.rankChartRadioProvince}}</el-radio-button>
          </el-radio-group>
          <div class="null-txt" :class="{hide:nullHide}">{{language.nullTxt}}</div>
        </div>
      </el-col>
    </el-row>
    <el-row class="chart-row">
      <el-col :span="24">
        <div class="chart-card">
          <div id="curveChart" class="chart" :class="{hide:chartHide}"></div>
          <el-button @click="handleShowDiff" icon="plus" class="addDiffBtn" :class="{hide:chartHide}" size="small">{{language.addDiffBtn}}</el-button>
          <div class="null-txt" :class="{hide:nullHide}">{{language.nullTxt}}</div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  // 引入echarts
  import echarts from 'echarts'
  export default {
    data() {
      return {
        language: {
          choseFile: '',
          drPH: '',
          fileUid: '',
          fileTag: '',
          fileName: '',
          fileTime: '',
          fileViews: ':',
          tabFileName: '',
          chose: '',
          mapStartName: '',
          mapTxt: '',
          rankChartRadioCity: '',
          rankChartRadioProvince: '',
          rankTxt: '',
          rankBar: '',
          curveChartTxt: '',
          nullTxt: '',
          addDiffBtn: '',
          noFileConfimTxt: '',
          noFileConfimTitle: '',
          noFileConfimSub: ''
        },
        dateRangeVal: '', // 日期范围
        fileId: '', //当前文件id
        fileUid: '', //当前文件uid
        fileTag: '', //当前文件标签
        fileName: '', //当前文件名称
        fileTime: '', //当前文件生成时间
        fileViews: '', //当前文件访问量
        diffFileName: '', //对比文件名
        diffFileId: '', //对比文件id
        dialogVisible: false, // 弹出框的显示隐藏
        dialogDiffVisible: false,
        gridData: [], // 弹出框表格数据
        totalElements: 0, //总条目数
        currentPage: 1, // 当前页数
        pageSize: 10, // 每页显示条目数
        map: '', // 地图对象
        geoCoordMap: '', //地图坐标数据
        diffData: '', //地图扩散数据
        rankChartRadio: '1', // 排行图切换
        rankChart: '', // 访问量排行图对象 
        rankCityData: [], // 排行城市数据
        rankCountData: [], // 排行数据
        curveChart: '', // 文件访问量大数据对象
        curveData: '', // 文件访问量数据
        curveDiffData: '', // 文件访问量对比文件数据
        curveDate: '', // 文件访问量日期数据
        nullHide: true,
        chartHide: false,
        mapJsonType: 'china.json'
      }
    },
    methods: {
      renderlang(response) {
        this.language = response.data.data;
      },
      handleSizeChange(pageSize) {
        this.pageSize = pageSize;
        this.getGridData(1);
      },
      handleCurrentChange(currentPage) {
        this.getGridData(currentPage);
      },
      // 选择文件弹出框
      handleShowFile() {
        this.dialogVisible = true;
        this.getGridData(1);
      },
      handleShowDiff() {
        this.dialogDiffVisible = true;
        this.getGridData(1);
      },
      // 获取页面数据
      getData() {
        const vm = this;
        let loading = vm.$loading();
        vm.$http.all([vm.$http.get('/wyfx/ft/china.json'),
            vm.$http.get('/wyfx/fileData/getCityAndPercent', {
              params: {
                fileId: vm.fileId,
                startTime: vm.getStartTime(),
                endTime: vm.getEndTime(),
                type: 'china'
              }
            }),
            vm.$http.get('/wyfx/fileData/getCityOrRegionAndCount', {
              params: {
                fileId: vm.fileId,
                flag: vm.rankChartRadio,
                startTime: vm.getStartTime(),
                endTime: vm.getEndTime()
              }
            }), vm.$http.get('/wyfx/fileData/getCountByDay', {
              params: {
                fileId: vm.fileId,
                startTime: vm.getStartTime(),
                endTime: vm.getEndTime()
              }
            })
          ])
          .then(vm.$http.spread(function (mapJson, map, rank, curve) {
            console.log(mapJson, map);
            vm.renderMap(mapJson, map, 'china');
            vm.renderRank(rank);
            vm.renderCurveChart(curve, 0);
            loading.close();
          }));
      },
      // 选择文件click
      handleChose(index, row) {
        const vm = this;
        this.fileId = row.id;
        this.fileUid = row.uuid;
        this.fileName = row.fileName;
        this.fileTag = row.tag;
        this.fileTime = row.time;
        this.dialogVisible = false;
        this.getData();
      },
      handleDiffChose(index, row) {
        const vm = this;
        this.diffFileId = row.id;
        this.diffFileName = row.fileName;
        this.dialogDiffVisible = false;
        vm.$httpi.get('/wyfx/fileData/getCountByDay', {
            params: {
              fileId: vm.diffFileId,
              startTime: vm.getStartTime(),
              endTime: vm.getEndTime()
            }
          })
          .then(function (respone) {
            vm.renderCurveChart(respone, 1);
          });
      },
      // 查询文件列表
      getGridData(pageNo) {
        const vm = this;
        this.$httpi.get('/wyfx/file/getAllFileSignByCondition', {
            params: {
              pageNo: pageNo,
              fileTypeId: null,
              startTime: null,
              endTime: null,
              pageSize: vm.pageSize
            }
          })
          .then(function (response) {
            vm.gridData = response.data.data.content;
            vm.totalElements = response.data.data.totalElements;
            vm.currentPage = pageNo;
          })
      },
      // 日期改变触发
      handleDataChange(val) {
        const vm = this;
        if (this.dateRangeVal == undefined) {
          this.dateRangeVal = new Array();
          this.dateRangeVal.push(null);
          this.dateRangeVal.push(null);
        }
        this.getData();
      },
      // 获取开始时间
      getStartTime() {
        if (this.dateRangeVal[0] != '' && this.dateRangeVal[0] != null && this.dateRangeVal[0] != undefined) {
          return this.$formatDate(this.dateRangeVal[0], 'yyyy-MM-dd');
        } else {
          return null;
        }
      },
      // 获取结束时间
      getEndTime() {
        if (this.dateRangeVal[1] != '' && this.dateRangeVal[1] != null && this.dateRangeVal[1] != undefined) {
          return this.$formatDate(this.dateRangeVal[1], 'yyyy-MM-dd');
        } else {
          return null;
        }
      },
      renderFileNewest(response) {
        const vm = this;
        if (response.data.code == 300) {
          vm.$confirm(vm.language.noFileConfimTxt, vm.language.noFileConfimTitle, {
            showCancelButton: false,
            confirmButtonText: vm.language.noFileConfimSub,
            lockScroll: true,
            type: 'warning'
          }).then(() => {
            vm.$router.push({
              name: 'FileMake'
            });
          }).catch(() => {
            vm.$router.push({
              name: 'FileMake'
            });
          });
        } else {
          vm.fileId = response.data.data.id;
          vm.fileUid = response.data.data.uuid;
          vm.fileName = response.data.data.fileName;
          vm.fileTag = response.data.data.tag;
          vm.fileTime = response.data.data.time;
          // 设置日期时间
          vm.setDataRange();
        }
      },
      renderMap(mapJson, response, mapType) {
        const vm = this;
        vm.fileViews = response.data.data.count;
        if (response.data.data.cityPercent.length < 1) {
          vm.nullHide = false;
          vm.chartHide = true;
          return
        } else {
          vm.nullHide = true;
          vm.chartHide = false;
        }
        vm.geoCoordMap = response.data.data.city;
        vm.diffData = response.data.data.cityPercent;
        var planePath =
          'path://M1705.06,1318.313v-89.254l-319.9-221.799l0.073-208.063c0.521-84.662-26.629-121.796-63.961-121.491c-37.332-0.305-64.482,36.829-63.961,121.491l0.073,208.063l-319.9,221.799v89.254l330.343-157.288l12.238,241.308l-134.449,92.931l0.531,42.034l175.125-42.917l175.125,42.917l0.531-42.034l-134.449-92.931l12.238-241.308L1705.06,1318.313z';
        var convertData = function (data) {
          var res = [];
          for (var i = 0; i < data.length; i++) {
            var dataItem = data[i];
            var fromCoord = vm.geoCoordMap[dataItem[0].name];
            var toCoord = vm.geoCoordMap[dataItem[1].name];
            if (fromCoord && toCoord) {
              res.push({
                fromName: dataItem[0].name,
                toName: dataItem[1].name,
                coords: [fromCoord, toCoord]
              });
            }
          }
          return res;
        };
        var color = ['#38b3fc', '#ffa022', '#a6c84c'];
        var series = [];
        [
          [vm.diffData[0][0].name, vm.diffData]
        ].forEach(function (item, i) {
          series.push({
            name: vm.language.mapStartName + item[0],
            type: 'lines',
            zlevel: 1,
            effect: {
              show: true,
              period: 6,
              trailLength: 0.7,
              color: '#fff',
              symbolSize: 3
            },
            lineStyle: {
              normal: {
                color: color[i],
                width: 0,
                curveness: 0.2
              }
            },
            data: convertData(item[1])
          }, {
            name: vm.language.mapStartName + item[0],
            type: 'lines',
            zlevel: 2,
            symbol: ['none', 'arrow'],
            symbolSize: 10,
            effect: {
              show: true,
              period: 6,
              trailLength: 0,
              symbol: planePath,
              symbolSize: 15
            },
            lineStyle: {
              normal: {
                color: color[i],
                width: 1,
                opacity: 0.6,
                curveness: 0.2
              }
            },
            data: convertData(item[1])
          }, {
            name: vm.language.mapStartName + item[0],
            type: 'effectScatter',
            coordinateSystem: 'geo',
            zlevel: 2,
            rippleEffect: {
              brushType: 'stroke'
            },
            label: {
              normal: {
                show: true,
                position: 'right',
                formatter: '{b}'
              }
            },
            symbolSize: function (val) {
              return val[2] / 8;
            },
            itemStyle: {
              normal: {
                color: color[i]
              }
            },
            data: item[1].map(function (dataItem) {
              return {
                name: dataItem[1].name,
                value: vm.geoCoordMap[dataItem[1].name].concat([dataItem[1].value])
              };
            })
          });
        });
        echarts.registerMap(mapType, mapJson.data);
        // 绘制图表
        vm.map.setOption({
          backgroundColor: '#fff',
          title: {
            text: vm.language.mapTxt
          },
          tooltip: {
            trigger: 'item'
          },
          // legend: {
          //   orient: 'vertical',
          //   top: 'bottom',
          //   left: 'right',
          //   data: ['北京 Top10', '上海 Top10', '广州 Top10'],
          //   textStyle: {
          //     color: '#364760'
          //   },
          //   selectedMode: 'single'
          // },
          geo: {
            center: [116.46, 39.92],
            left: 'center',
            top: 'middle',
            map: mapType,
            label: {
              emphasis: {
                show: false
              }
            },
            roam: true,
            itemStyle: {
              normal: {
                areaColor: '#364760',
                borderColor: '#404a59'
              },
              emphasis: {
                areaColor: '#2a394f'
              }
            }
          },
          series: series
        });
      },
      // 访问量排行
      renderRank(response) {
        const vm = this;
        vm.rankCityData = [];
        vm.rankCountData = [];
        for (let i in response.data.data) {
          vm.rankCityData.push(response.data.data[i].area);
          vm.rankCountData.push(response.data.data[i].count);
        }
        // 绘制图表
        vm.rankChart.setOption({
          color: ['#38b3fc'],
          title: {
            text: vm.language.rankTxt
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'value',
            boundaryGap: [0, 0.01]
          },
          yAxis: {
            type: 'category',
            data: vm.rankCityData
          },
          series: [{
            name: vm.language.rankBar,
            type: 'bar',
            data: vm.rankCountData
          }]
        });
      },
      // 排行切换城市、省份
      handleRankRadioChange(val) {
        const vm = this;
        vm.$httpi.get('/wyfx/fileData/getCityOrRegionAndCount', {
            params: {
              fileId: vm.fileId,
              flag: vm.rankChartRadio,
              startTime: vm.getStartTime(),
              endTime: vm.getEndTime()
            }
          })
          .then(function (response) {
            vm.renderRank(response);
          });
      },
      // 地图切换国内、全球
      handleMapChange(val) {
        let mapType = '';
        const vm = this;
        if (val == 'china.json') {
          mapType = 'china';
        } else if (val == 'world.json') {
          mapType = 'world';
        }
        let loading = vm.$loading();
        vm.$http.all([vm.$http.get('/wyfx/ft/' + this.mapJsonType),
            vm.$http.get('/wyfx/fileData/getCityAndPercent', {
              params: {
                fileId: vm.fileId,
                startTime: vm.getStartTime(),
                endTime: vm.getEndTime(),
                type: mapType
              }
            })
          ])
          .then(vm.$http.spread(function (mapJson, map) {
            console.log(mapJson, map);
            vm.renderMap(mapJson, map, mapType);
            loading.close();
          }));
      },
      // 访问量面积图
      renderCurveChart(response, flag) {
        const vm = this;
        if (flag == 0) {
          vm.curveData = response.data.data.count;
          vm.curveDate = response.data.data.time;
          vm.curveDiffData = [];
          vm.diffFileName = '';
        } else {
          vm.curveDiffData = response.data.data.count;
        }
        vm.curveChart.setOption({
          tooltip: {
            trigger: 'axis',
            position: function (pt) {
              return [pt[0], '10%'];
            }
          },
          title: {
            text: vm.language.curveChartTxt,
          },
          legend: {
            data: [vm.fileName, vm.diffFileName]
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: vm.curveDate
          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, '100%']
          },
          dataZoom: [{
            type: 'inside',
            start: 0,
            end: 10
          }, {
            start: 0,
            end: 10,
            handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
            handleSize: '80%',
            handleStyle: {
              color: '#fff',
              shadowBlur: 3,
              shadowColor: 'rgba(0, 0, 0, 0.6)',
              shadowOffsetX: 2,
              shadowOffsetY: 2
            }
          }],
          series: [{
              name: vm.fileName,
              type: 'line',
              smooth: true,
              symbol: 'none',
              sampling: 'average',
              itemStyle: {
                normal: {
                  color: 'rgb(131, 184, 247)'
                }
              },
              areaStyle: {
                normal: {
                  color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgb(131, 184, 247)'
                  }, {
                    offset: 1,
                    color: 'rgb(247, 251, 255)'
                  }])
                }
              },
              data: vm.curveData
            },
            {
              name: vm.diffFileName,
              type: 'line',
              smooth: true,
              symbol: 'none',
              sampling: 'average',
              itemStyle: {
                normal: {
                  color: 'rgb(255, 189, 119)'
                }
              },
              areaStyle: {
                normal: {
                  color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgb(255, 189, 119)'
                  }, {
                    offset: 1,
                    color: 'rgb(255, 251, 246)'
                  }])
                }
              },
              data: vm.curveDiffData
            }
          ]
        });
      },
      // 设置日期时间
      setDataRange() {
        let arr = new Array();
        let end = new Date();
        let start = new Date();
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
        arr.push(start);
        arr.push(end);
        this.dateRangeVal = arr;
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      // 定义图表dom
      this.map = echarts.init(document.getElementById('map'));
      this.curveChart = echarts.init(document.getElementById('curveChart'));
      this.rankChart = echarts.init(document.getElementById('rankChart'));
      vm.$http.all([vm.$http.get('/wyfx/page/getPageMsg', {
          params: {
            msg: 'TrackAnalysis'
          }
        }), vm.$http.get('/wyfx/file/getFileNewest')])
        .then(vm.$http.spread(function (lang, file) {
          vm.renderlang(lang);
          vm.renderFileNewest(file);
          loading.close();
        }));
    }
  }

</script>


<style scoped>
  .right-main {
    height: calc(100% - 20px);
    overflow: auto;
    padding: 10px;
  }

  .tool-bar {
    height: 55px;
    line-height: 55px;
    background-color: #fff;
    padding: 0 20px;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04)
  }

  .info-bar {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    margin-top: 5px;
    color: #687189;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04)
  }

  .info-mar {
    margin-top: 5px;
  }

  .file-views {
    color: #d57b81;
    font-size: 18px;
    margin-top: 8px;
  }

  .pagination {
    margin-top: 5px;
    text-align: center;
  }

  .chart-card {
    position: relative;
    height: 450px;
    background-color: #fff;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }

  .chart-row {
    margin-top: 5px;
  }

  .chart {
    width: 100%;
    height: 100%;
  }

  .rankChart-radio {
    position: absolute;
    top: 10px;
    right: 35px;
  }

  .null-txt {
    position: absolute;
    top: calc(50% - 10px);
    left: calc(50% - 25px);
    font-size: 14px;
    color: #687189;
  }

  .hide {
    display: none;
  }

  .addDiffBtn {
    position: absolute;
    top: 10px;
    right: 10px;
  }

</style>



