<template>
  <div class="right-main">
    <!-- 修改备注弹出框 -->
    <el-dialog :visible.sync="dialogRemarkVisible" :title="language.digTitle" size="tiny" :before-close="beforeClose">
      <el-input type="textarea" :rows="5" :placeholder="language.digTxtArea" v-model="textarea">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogRemarkSub">{{language.dialogRemarkSub}}</el-button>
      </span>
    </el-dialog>
    <!-- 修改备注弹出框end -->
    <div class="outer">
      <div class="top">
        <h3>{{language.topTitle}}</h3>
        <el-row class="top-info">
          <el-col :span="12">
            <div>{{language.topCode}}</div>
          </el-col>
          <el-col :span="12">
            <div>{{language.address}}</div>
          </el-col>
        </el-row>
      </div>
      <div class="tool-bar">
        <el-upload class="upload-demo" :action="this.$rootPath +'/wyfx/fileType/importPSO'" :on-success="handleSuces" :on-error="handleErr"
          :before-upload="beforeUpload" :show-file-list="false">
          <el-button slot="trigger" type="primary">{{language.import}}</el-button>
          <el-button @click="handleExport">{{language.export}}</el-button>
        </el-upload>
      </div>
      <div class="inner">
        <el-table :data="tableData" @select="selectRow" @select-all="selectRow" class="table" style="width: 100%;" height="100%"
          stripe>
          <el-table-column type="selection" width="50">
          </el-table-column>
          <el-table-column :label="language.tabSupFormat" min-width="100">
            <template scope="scope">
              <pre class="table-pre">{{scope.row.swf}}</pre>
            </template>
          </el-table-column>
          <el-table-column prop="type" :label="language.tabAssistDes">
          </el-table-column>
          <el-table-column prop="versionS" :label="language.tabVersion">
          </el-table-column>
          <el-table-column prop="sign" :label="language.tabMark">
          </el-table-column>
          <el-table-column prop="watermark" :label="language.tabWatermark">
          </el-table-column>
          <el-table-column :label="language.tabStatus" >
            <template scope="scope">
              <span class="green">{{scope.row.status}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="time" :label="language.tabTime" min-width="150">
          </el-table-column>
          <el-table-column :label="language.tabDes">
            <template scope="scope">
              <span class="red">{{scope.row.description}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="remark" :label="language.tabRemark">
          </el-table-column>
          <el-table-column width="50">
            <template scope="scope">
              <el-button size="small" type="text" @click="handleEdit(scope.$index, scope.row)">
                修改</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="pagination">
          <el-pagination layout="sizes, prev, pager, next, total" :current-page="currentPage" :page-size="pageSize" :page-sizes="[20, 50, 100, 200, 400]"
            :total="totalElements" @current-change="handleCurrentChange" @size-change="handleSizeChange"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {
          topTitle: '',
          topCode: '',
          address: '',
          import: '',
          export: '',
          tabSupFormat: '',
          tabAssistDes: '',
          tabVersion: '',
          tabMark: '',
          tabWatermark: '',
          tabStatus: '',
          tabTime: '',
          tabDes: '',
          tabRemark: '',
          support: '',
          noSup: '',
          algorithmStatus: '',
          uploadErrMes: '',
          digYes: '',
          digNo: '',
          digTitle: '',
          digTxtArea: '',
          dialogRemarkSub: '',
          editSuc: '',
          editErr: '',
          importSuc: '',
          importErr: '',
          exportMes: '',
          exportSuc: '',
          exportErr: '',
          msgBoxTitle: '',
          exportWarn: ''
        },
        tableData: [],
        totalElements: 0, //总条目数
        currentPage: 1, // 当前页数
        pageSize: 20, // 每页显示条目数
        selection: '[]', // 当前选中行
        dialogRemarkVisible: false,
        loading: '',
        textarea: '',
        editRowId: '' // 点击修改备注时 当前行的id
      }
    },
    methods: {
      renderlang(response) {
        this.language = response.data.data;
      },
      // 查询数据
      handleGetTableData(pageNo) {
        const vm = this;
        vm.$httpi.get('/wyfx/fileType/getAllFileType', {
            params: {
              pageNo: pageNo,
              pageSize: vm.pageSize
            }
          })
          .then(function (response) {
            vm.renderTable(response);
          });
      },
      renderTable(response) {
        const vm = this;
        vm.tableData = response.data.data.content;
        for (let i in response.data.data.content) {
          if (response.data.data.content[i].sign == 1) {
            vm.tableData[i].sign = vm.language.support;
          } else {
            vm.tableData[i].sign = vm.language.noSup;
          }
          if (response.data.data.content[i].watermark == 1) {
            vm.tableData[i].watermark = vm.language.support;
          } else {
            vm.tableData[i].watermark = vm.language.noSup;
          }
          if (response.data.data.content[i].status == 1) {
            vm.tableData[i].status = vm.language.algorithmStatus;
          } else {
            vm.tableData[i].status = '';
          }
          vm.tableData[i].swf = vm.tableData[i].swf.replace(/,/g, "\n");
        }
        vm.totalElements = response.data.data.totalElements;
        vm.currentPage = response.data.data.pageNo;
      },
      handleSizeChange(pageSize) {
        this.pageSize = pageSize;
        this.handleGetTableData(1);
      },
      handleCurrentChange(currentPage) {
        this.handleGetTableData(currentPage);
      },
      // 表格行选择
      selectRow(selection) {
        let arr = new Array();
        for (let i in selection) {
          arr.push(selection[i].id);
        }
        this.selection = JSON.stringify(arr);
      },
      beforeUpload() {
        this.loading = this.$loading({
          fullscreen: true
        });
      },
      // 修改备注
      handleEdit(index, row) {
        this.editRowId = row.id;
        this.dialogRemarkVisible = true;
      },
      // 修改备注关闭时
      beforeClose() {
        this.editRowId = '';
        this.textarea = '';
        this.dialogRemarkVisible = false;
      },
      // 修改备注提交
      dialogRemarkSub() {
        const vm = this;
        this.$httpi.get('/wyfx/fileType/updateRemark', {
            params: {
              id: vm.editRowId,
              remark: vm.textarea
            }
          })
          .then(function (response) {
            if (response.data.code == '200') {
              vm.$message.success(vm.language.editSuc);
              vm.handleGetTableData(vm.currentPage);
            } else {
              vm.$message.error(vm.language.editErr);
            }
            vm.editRowId = '';
            vm.textarea = '';
            vm.dialogRemarkVisible = false;
          })
      },
      // 文件上传成功
      handleSuces(response, file, fileList) {
        this.loading.close();
        const vm = this;
        if (response.code == 107) {
          this.$confirm(response.msg, vm.language.msgBoxTitle, {
            confirmButtonText: vm.digYes,
            cancelButtonText: vm.digNo,
            type: 'warning'
          }).then(() => {
            const vm = this;
            this.$httpi.get('/wyfx/fileType/updateVersion', {
                params: {
                  id: response.data.id,
                  flag: true
                }
              })
              .then(function (response) {
                if (response.data.code == '200') {
                  vm.$message.success(vm.language.editSuc);
                  vm.handleGetTableData(1);
                } else {
                  vm.$message.error(vm.language.editErr);
                }
              });
          }).catch(() => {
            this.$httpi.get('/wyfx/fileType/updateVersion', {
              params: {
                id: response.data.id,
                flag: false
              }
            });
          });
        } else if (response.code == 200) {
          this.$message.success(response.msg);
          this.handleGetTableData(1);
        } else if (response.code == 500) {
          this.$message.error(response.msg);
        } else {
          console.log(response);
          this.$router.push({
            name: 'Error'
          });
        }
      },
      // 文件上传失败
      handleErr(err, file, fileList) {
        this.$message.error(this.language.uploadErrMes);
      },
      // 算法导出
      handleExport() {
        const vm = this;
        if (this.selection == '[]') {
          this.$message({
            message: vm.language.exportMes,
            type: 'warning'
          });
        } else {
          this.$confirm(vm.language.exportWarn, vm.language.msgBoxTitle, {
            confirmButtonText: vm.digYes,
            cancelButtonText: vm.digNo,
            type: 'warning'
          }).then(() => {
            const vm = this;
            this.$httpi.get('/wyfx/fileType/exportPSO', {
              params: {
                ids: vm.selection
              }
            }).then(function (response) {
              if (response.data.code == '200') {
                vm.$message.success(vm.language.exportSuc);
                vm.handleGetTableData(1);
              } else {
                vm.$message.error(vm.language.exportErr);
              }
            });
          }).catch(() => {

          });
        }
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      vm.$http.all([vm.$http.get('/wyfx/page/getPageMsg', {
          params: {
            msg: 'AlgorithmManage'
          }
        }), vm.$http.get('/wyfx/fileType/getAllFileType', {
          params: {
            pageNo: 1,
            pageSize: vm.pageSize
          }
        })])
        .then(vm.$http.spread(function (lang, table) {
          vm.renderlang(lang);
          vm.renderTable(table);
          loading.close();
        }));
    }
  }

</script>


<style scoped>
  .right-main {
    height: calc(100% - 20px);
    padding: 10px;
  }

  .outer {
    width: 100%;
    height: 100%;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }

  .top {
    padding: 20px;
    background-color: #fff;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    color: #687189;
  }

  .top-info {
    margin-top: 10px;
  }

  .tool-bar {
    margin-top: 2px;
    width: calc(100% - 40px);
    height: 55px;
    line-height: 55px;
    background-color: #fff;
    padding: 0 20px;
  }

  .inner {
    margin-top: 2px;
    width: calc(100% - 40px);
    height: calc(100% - 195px);
    background-color: #fff;
    padding: 20px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }

  .table {
    width: 100%;
    height: calc(100% - 22px);
  }

  .pagination {
    margin-top: 5px;
    text-align: center;
  }


  .green {
    color: #13ce66;
  }

  .red {
    color: #ff4949;
  }

  .table-pre {
    white-space: pre-wrap;
    word-wrap: break-word;
  }

</style>



