<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    name: 'app',
    mounted() {
      this.$router.push({
        name: 'FileRecord'
      });
    }
  }
</script>

<style>
  /*複用樣式*/
  .clearfix:after {
    content: "\200B";
    display: block;
    height: 0;
    clear: both;
  }
  .fl {
    float: left;
  }
  .fr {
    float: right;
  }
  #app {
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  }
  * {
    margin: 0;
    padding: 0;
  }
  html,
  body,
  #app {
    height: 100%;
    min-width: 1360px;
  }
  .fullHeight {
    height: 100%;
  }
</style>

<style>
  /*重写element  */
  .el-submenu__title {
    background-color: #364760;
    height: 45px;
    line-height: 45px;
    color: #d9e1e5;
  }
  .el-submenu__title:hover {
    background-color: #2a394f;
    color: #fff;
  }
  .el-submenu__icon-arrow {
    color: #d9e1e5
  }
  .el-menu {
    border-radius: 0;
  }
</style>




