<template>
  <div class="right-main">
    <!-- 修改备注弹出框 -->
    <el-dialog :visible.sync="dialogRemarkVisible" :title="language.digTitle" size="tiny" :before-close="beforeClose">
      <el-input type="textarea" :rows="5" :placeholder="language.digTxtArea" v-model="textarea">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogRemarkSub">{{language.dialogRemarkSub}}</el-button>
      </span>
    </el-dialog>
    <!-- 修改备注弹出框end -->
    <div class="outer">
      <div class="tool-bar">
        <el-input v-model="inputIp" :placeholder="language.inpIpPH" class="search-input"></el-input>
        <el-input v-model="inputName" :placeholder="language.inpNamePH" class="search-input"></el-input>
        <el-select clearable v-model="fileTypeVal" :placeholder="language.ftOptionPH">
          <el-option v-for="item in fileTypeOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-date-picker v-model="dateRangeVal" type="daterange" :placeholder="language.drPH"></el-date-picker>
        <el-radio-group v-model="radioVal" style="margin-left: 5px">
          <el-radio :label="1">{{language.fileType1}}</el-radio>
          <el-radio :label="2">{{language.fileType2}}</el-radio>
        </el-radio-group>
        <el-button class="inqBtn" type="primary" @click="getTableData(1)">{{language.inquire}}</el-button>
      </div>
      <div class="inner-with-bar">
        <el-table :data="tableData" class="table" style="width: 100%;" height="100%" stripe>
          <el-table-column prop="filename" :label="language.tabFileName">
          </el-table-column>
          <el-table-column prop="swf" :label="language.tabFileFormat" width="100">
          </el-table-column>
          <el-table-column prop="network" :label="language.tabInternet" width="100">
          </el-table-column>
          <el-table-column prop="time" :label="language.tabTime">
          </el-table-column>
          <el-table-column prop="ip" :label="language.tabDeviceIp">
          </el-table-column>
          <el-table-column prop="device" :label="language.tabDeviceName">
          </el-table-column>
          <!-- <el-table-column prop="fireWallNumber" :label="language.tabAct">
          </el-table-column> -->
          <el-table-column :label="language.tabShowFile" width="110">
            <template scope="scope">
                <el-button size="small" type="text" @click="handleDown(scope.$index, scope.row)">{{language.tabDown}}</el-button>
            </template>
          </el-table-column>
          <el-table-column :label="language.tabStatus" width="85">
            <template scope="scope">
              <el-tag v-if="scope.row.status === 1" type="success">{{language.tabStatusYes}}</el-tag>
              <el-tag v-if="scope.row.status === 0" type="danger">{{language.tabStatusNo}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column :label="language.tableEditStatus" width="100">
            <template scope="scope">
              <el-tag v-if="scope.row.updateStatus === 1" type="success">{{language.tabEditY}}</el-tag>
              <el-tag v-if="scope.row.updateStatus === 0" type="danger">{{language.tabEditN}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="remark" :label="language.tabRemark">
          </el-table-column>
          <el-table-column width="60">
            <template scope="scope">
              <el-button size="small" type="text" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="pagination">
          <el-pagination layout="sizes, prev, pager, next, total" :current-page="currentPage" :page-size="pageSize" :page-sizes="[20, 50, 100, 200, 400]"
            :total="totalElements" @current-change="handleCurrentChange" @size-change="handleSizeChange"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {},
        inputIp: '',
        inputName: '',
        fileTypeOptions: [],
        fileTypeVal: '',
        dateRangeVal: '',
        radioVal: 1,
        tableData: [],
        totalElements: 0, //总条目数
        currentPage: 1, // 当前页数
        pageSize: 20, // 每页显示条目数
        selection: [], // 当前选择得行
        dialogRemarkVisible: false,
        textarea: '',
        editRowId: '' // 点击修改备注时 当前行的id
      }
    },
    methods: {
      // 获取开始时间
      getStartTime() {
        if (this.dateRangeVal[0] != '' && this.dateRangeVal[0] != null && this.dateRangeVal[0] != undefined) {
          return this.$formatDate(this.dateRangeVal[0], 'yyyy-MM-dd');
        } else {
          return null;
        }
      },
      // 获取结束时间
      getEndTime() {
        if (this.dateRangeVal[1] != '' && this.dateRangeVal[1] != null && this.dateRangeVal[1] != undefined) {
          return this.$formatDate(this.dateRangeVal[1], 'yyyy-MM-dd');
        } else {
          return null;
        }
      },
      getTableData(pageNo) {
        const vm = this;
        if (vm.fileTypeVal == '') {
          vm.fileTypeVal = null;
        }
        vm.$httpi.get('/wyfx/fwFile/getAllFwFileOrSign', {
            params: {
              pageNo: pageNo,
              pageSize: vm.pageSize,
              flag: vm.radioVal,
              fwTypeId: vm.fileTypeVal,
              startTime: vm.getStartTime(),
              endTime: vm.getEndTime(),
              filename: vm.inputName,
              ip: vm.inputIp
            }
          })
          .then(function (response) {
            vm.renderTable(response);
          });
      },
      renderTable(response) {
        this.tableData = response.data.data.content;
        this.totalElements = response.data.data.totalElements;
        this.currentPage = response.data.data.pageNo;
      },
      handleSizeChange(pageSize) {
        this.pageSize = pageSize;
        this.getTableData(1);
      },
      handleCurrentChange(currentPage) {
        this.getTableData(currentPage);
      },
      // 修改备注
      handleEdit(index, row) {
        this.editRowId = row.id;
        this.textarea = row.remark;
        this.dialogRemarkVisible = true;
      },
      // 修改备注关闭时
      beforeClose() {
        this.editRowId = '';
        this.textarea = '';
        this.dialogRemarkVisible = false;
      },
      // 修改备注提交
      dialogRemarkSub() {
        const vm = this;
        this.$httpi.get('/wyfx/fwFile/updateRemark', {
            params: {
              id: vm.editRowId,
              remark: vm.textarea
            }
          })
          .then(function (response) {
            if (response.data.code == '200') {
              vm.$message.success(vm.language.editSuc);
              vm.getTableData(vm.currentPage);
            } else {
              vm.$message.error(vm.language.editErr);
            }
            vm.editRowId = '';
            vm.textarea = '';
            vm.dialogRemarkVisible = false;
          })
      },
      // 下载文件
      handleDown(index, row) {
        const vm = this;
        this.$httpi.get('/wyfx/fwFile/isExistFile', {
            params: {
              fwFileId: row.id
            }
          })
          .then(function (response) {
            if (response.data.code == '200') {
              window.open(vm.$rootPath + '/wyfx/fwFile/downLoadFwFile?fileId=' + row.id);
            } else {
              vm.$message.error(vm.language.fileMiss);
            }
          })
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      vm.$http.all([vm.$http.get('/wyfx/fwFile/getFileType'),
          vm.$http.get('/wyfx/fwFile/getAllFwFileOrSign', {
            params: {
              pageNo: 1,
              pageSize: vm.pageSize,
              flag: vm.radioVal,
              fwTypeId: null,
              startTime: null,
              endTime: null,
              filename: null,
              ip: null,
            }
          }),
          vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'Detailed'
            }
          })
        ])
        .then(vm.$http.spread(function (fileType, tableData, language) {
          // 获取文本信息
          vm.language = language.data.data;
          // 获取支持文件格式
          for (let i in fileType.data.data) {
            var thisOne = [];
            thisOne.value = fileType.data.data[i].id;
            thisOne.label = fileType.data.data[i].type;
            vm.fileTypeOptions.push(thisOne);
          }
          // 获取表格数据
          console.log(tableData);
          vm.renderTable(tableData);
          loading.close();
        }));
    }
  }

</script>


<style scoped>
  .right-main {
    height: calc(100% - 20px);
    padding: 10px;
  }

  .outer {
    width: 100%;
    height: 100%;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }

  .tool-bar {
    width: calc(100% - 40px);
    height: 55px;
    line-height: 55px;
    background-color: #fff;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    padding: 0 20px;
  }

  .inner-with-bar {
    margin-top: 2px;
    width: calc(100% - 40px);
    height: calc(100% - 97px);
    background-color: #fff;
    padding: 20px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }

  .inqBtn {
    margin-left: 11px;
  }

  .table {
    width: 100%;
    height: calc(100% - 22px);
  }

  .pagination {
    margin-top: 5px;
    text-align: center;
  }

  .search-input {
    width: 150px;
  }

</style>



