<template>
  <div class="right-main">
    <div class="outer">
      <div class="tool-bar">
        <el-select clearable v-model="fileTypeVal" :placeholder="language.ftOptionPH">
          <el-option v-for="item in fileTypeOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-date-picker v-model="dateRangeVal" type="daterange" :placeholder="language.drPH"></el-date-picker>
        <el-button class="inqBtn" type="primary" @click="getTableData(1)">{{language.inquire}}</el-button>
        <el-button @click="handleDownFiles">{{language.export}}</el-button>
      </div>
      <div class="inner-with-bar">
        <el-table :data="tableData" @select="selectRow" @select-all="selectRow" class="table" style="width: 100%;" height="100%" stripe>
          <el-table-column type="selection" width="50">
          </el-table-column>
          <el-table-column prop="uuid" label="ID">
          </el-table-column>
          <el-table-column prop="fileName" :label="language.tabFileName">
          </el-table-column>
          <el-table-column prop="tag" :label="language.tabTag">
          </el-table-column>
          <el-table-column prop="remark" :label="language.tabRemark">
          </el-table-column>
          <el-table-column :label="language.tabMark">
            <template scope="scope">
                          <div v-if="scope.row.fireWallNumber === true" class="green">
                            {{language.you}}
                          </div>
                          <div v-else class="red">
                            {{language.wu}}
                          </div>
</template>
          </el-table-column>
          <el-table-column :label="language.tabTrack">
<template scope="scope">
  <div v-if="scope.row.status === 1" class="green">
    {{language.you}}
  </div>
  <div v-else class="red">
    {{language.wu}}
  </div>
</template>
          </el-table-column>
        </el-table>
        <div class="pagination">
          <el-pagination layout="sizes, prev, pager, next, total" :current-page="currentPage" :page-size="pageSize" :page-sizes="[20, 50, 100, 200, 400]"
            :total="totalElements" @current-change="handleCurrentChange" @size-change="handleSizeChange"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {
          ftOptionPH: '',
          drPH: '',
          inquire: '',
          export: '',
          tabFileName: '',
          tabTag: '',
          tabRemark: '',
          tabMark: '',
          downFilesMes: '',
          you: '',
          wu: ''
        },
        fileTypeOptions: [],
        fileTypeVal: '',
        dateRangeVal: '',
        tableData: [],
        totalElements: 0, //总条目数
        currentPage: 1, // 当前页数
        pageSize: 20, // 每页显示条目数
        selection: [] // 当前选择得行
      };
    },
    methods: {
      handleSizeChange(pageSize) {
        this.pageSize = pageSize;
        this.getTableData(1);
      },
      handleCurrentChange(currentPage) {
        this.getTableData(currentPage);
      },
      // 查询数据
      getTableData(pageNo) {
        const vm = this;
        let fileTypeId = null;
        let startTime = null;
        let endTime = null;
        if (this.fileTypeVal != '' && this.fileTypeVal != null && this.fileTypeVal != undefined) {
          fileTypeId = this.fileTypeVal;
        }
        if (this.dateRangeVal[0] != '' && this.dateRangeVal[0] != null && this.dateRangeVal[0] != undefined) {
          startTime = this.$formatDate(this.dateRangeVal[0], 'yyyy-MM-dd');
        }
        if (this.dateRangeVal[1] != '' && this.dateRangeVal[1] != null && this.dateRangeVal[1] != undefined) {
          endTime = this.$formatDate(this.dateRangeVal[1], 'yyyy-MM-dd');
        }
        this.$httpi.get('/wyfx/file/getAllFileSignOrWatermarkByCondition', {
            params: {
              pageNo: pageNo,
              fileTypeId: fileTypeId,
              startTime: startTime,
              endTime: endTime,
              pageSize: vm.pageSize
            }
          })
          .then(function(response) {
            vm.tableData = response.data.data.content;
            vm.totalElements = response.data.data.totalElements;
            vm.currentPage = pageNo;
          })
      },
      // 表格行选择
      selectRow(selection) {
        let arr = new Array();
        for (let i in selection) {
          arr.push(selection[i].id);
        }
        this.selection = JSON.stringify(arr);
      },
      // 下载文件
      handleDownFiles() {
        const vm = this;
        if (this.selection.length <= '0') {
          this.$message({
            message: vm.language.downFilesMes,
            type: 'warning'
          });
        } else {
          window.open(vm.$rootPath + '/wyfx/file/downLoadFile?fileIds=' + vm.selection);
        }
      }
    },
    mounted() {
      const vm = this;
       let loading = vm.$loading();
      let fileTypeId = null;
      let startTime = null;
      let endTime = null;
      if (this.fileTypeVal != '' && this.fileTypeVal != null && this.fileTypeVal != undefined) {
        fileTypeId = this.fileTypeVal;
      }
      if (this.dateRangeVal[0] != '' && this.dateRangeVal[0] != null && this.dateRangeVal[0] != undefined) {
        startTime = this.$formatDate(this.dateRangeVal[0], 'yyyy-MM-dd');
      }
      if (this.dateRangeVal[1] != '' && this.dateRangeVal[1] != null && this.dateRangeVal[1] != undefined) {
        endTime = this.$formatDate(this.dateRangeVal[1], 'yyyy-MM-dd');
      }
      vm.$http.all([vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'FileRecord'
            }
          }),
          vm.$http.get('/wyfx/file/getFileType'),
          vm.$http.get('/wyfx/file/getAllFileSignOrWatermarkByCondition', {
            params: {
              pageNo: 1,
              fileTypeId: fileTypeId,
              startTime: startTime,
              endTime: endTime,
              pageSize: vm.pageSize
            }
          })
        ])
        .then(vm.$http.spread(function(lang, file, table) {
          vm.language = lang.data.data;
          // 获取支持文件格式
          var data = file.data.data;
          for (let i in data) {
            var thisOne = [];
            thisOne.value = data[i].id;
            thisOne.label = data[i].type;
            vm.fileTypeOptions.push(thisOne);
          }
          // 获取表格数据
          vm.tableData = table.data.data.content;
          vm.totalElements = table.data.data.totalElements;
          vm.currentPage = 1;
          loading.close();
        }));
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .right-main {
    height: calc(100% - 20px);
    padding: 10px;
  }
  .outer {
    width: 100%;
    height: 100%;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }
  .tool-bar {
    width: calc(100% - 40px);
    height: 55px;
    line-height: 55px;
    background-color: #fff;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    padding: 0 20px;
  }
  .inner-with-bar {
    margin-top: 2px;
    width: calc(100% - 40px);
    height: calc(100% - 97px);
    background-color: #fff;
    padding: 20px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }
  .inqBtn {
    margin-left: 11px;
  }
  .table {
    width: 100%;
    height: calc(100% - 22px);
  }
  .pagination {
    margin-top: 5px;
    text-align: center;
  }
  .green {
    color: #13ce66;
  }
  .red {
    color: #ff4949;
  }
</style>



