<template>
  <div class="right-main">
    <div class="outer">
      <div class="inner">
        <el-row class="fullHeight">
          <el-col :span="12" class="fullHeight">
            <el-upload class="upload" ref="upload" :action="this.$rootPath + '/wyfx/file/upload'" :on-success="uploadSuccess" :on-remove="handleRemove"
              :file-list="fileList" multiple>
              <el-button slot="trigger" size="small">{{language.uploadFile}}</el-button>
              <div class="upload-title">{{language.uploadTitle}}</div>
              <span slot="tip" class="el-upload__tip">{{language.uploadTip}}<br>{{language.uploadTip2}}</span>
            </el-upload>
            <div class="fileMake">
              <div class="upload-title">{{language.makeTitle}}</div>
              <span slot="tip" class="el-upload__tip">{{language.makeTip}}</span>
              <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="makeform">
                <el-form-item :label="language.tagLabel" prop="tag">
                  <el-input v-model="ruleForm.tag" class="form-input"></el-input>
                </el-form-item>
                <el-form-item :label="language.remarkLabel" prop="remark">
                  <el-input v-model="ruleForm.remark" class="form-input"></el-input>
                </el-form-item>
                <el-form-item :label="language.typeLabel">
                  <el-checkbox-group v-model="ruleForm.type">
                    <el-checkbox label="1" name="type">{{language.codeLabel}}</el-checkbox>
                    <el-checkbox label="2" name="type">{{language.codeLabel2}}</el-checkbox>
                  </el-checkbox-group>
                </el-form-item>
                <el-form-item :label="language.inducedTxtLabel" prop="text">
                  <el-input v-model="ruleForm.text" class="form-input"></el-input>
                  <div class="el-upload__tip induced-tip">{{language.inducedTxtTip}}</div>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitForm('ruleForm')" :disabled="subBtnDis">{{language.makeBtn}}</el-button>
                </el-form-item>
              </el-form>
            </div>
          </el-col>
          <el-col :span="12" class="fullHeight">
            <div class="upload-title">{{language.okTitle}}
              <el-button size="small" @click="handleDownFiles">{{language.okBtn}}</el-button>
            </div>
            <el-table :data="tableData1" @select="selectRow" @select-all="selectRow" stripe style="width: 100%;" height="100%" class="table">
              <el-table-column type="selection" width="50">
              </el-table-column>
              <el-table-column prop="name" :label="language.fileNameLabel">
              </el-table-column>
              <el-table-column :label="language.status" width="150">
                <template scope="scope">
                  <el-tag :type="scope.row.status">{{scope.row.mes}}</el-tag>
                </template>
              </el-table-column>
            </el-table>
            <el-table :data="tableData2" stripe style="width: 100%;border-top: none;margin-top: 0" height="100%" class="table" :show-header="false">
              <el-table-column width="50">
              </el-table-column>
              <el-table-column prop="name" :label="language.fileNameLabel">
              </el-table-column>
              <el-table-column :label="language.status" width="150">
                <template scope="scope">
                  <el-tag :type="scope.row.status">
                    {{scope.row.mes}}</el-tag>
                </template>
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {},
        fileList: [],
        ruleForm: {
          tag: '',
          remark: '',
          type: ["1", "2"]
        },
        tableData1: [],
        tableData2: [],
        subBtnDis: true, // 开始标记按钮状态
        selection: '[]' //记录当前选中项的id
      };
    },
    methods: {
      // 删除已上传文件
      handleRemove(file, fileList) {
        if (fileList.length <= 0) {
          this.subBtnDis = true;
        }
        this.fileList = fileList;
      },
      // 文件上传成功
      uploadSuccess(response, file, fileList) {
        if (fileList.length > 0) {
          this.subBtnDis = false;
        }
        this.fileList = fileList;
        this.tableData1 = [];
        this.tableData2 = [];
      },
      // 开始标记
      submitForm() {
        const vm = this;
        let fileList = vm.fileList;
        if (fileList.length <= '0') {
          this.$message({
            message: vm.language.submitMes,
            type: 'warning'
          });
        } else if (vm.ruleForm.type.length < 1) {
          this.$message({
            message: vm.language.typeMes,
            type: 'warning'
          });
        } else {
          this.$loading({
            fullscreen: true,
            text: vm.language.loadingMes
          })
          let arr = new Array();
          for (let i in vm.fileList) {
            arr.push(vm.fileList[i].response.data.id);
          }
          arr = JSON.stringify(arr);
          this.$httpi.get('/wyfx/file/signFile', {
              params: {
                fileIds: arr,
                tag: vm.ruleForm.tag,
                remark: vm.ruleForm.remark,
                params: JSON.stringify(vm.ruleForm.type),
                text: vm.ruleForm.text,
              }
            })
            .then(function (response) {
              let data = response.data;
              for (let i in data) {
                let arr = new Array();
                arr.name = data[i].fileName;
                arr.id = data[i].id;
                if (data[i].status == 1) { // 成功
                  arr.status = 'success';
                  arr.mes = vm.language.fileSucc;
                  vm.tableData1.push(arr);
                } else if (data[i].status == 3) { // 格式错误
                  arr.status = 'danger';
                  arr.mes = vm.language.fileWrongFormat;
                  vm.tableData2.push(arr);
                } else if (data[i].status == 2) { // 失败
                  arr.status = 'danger';
                  arr.mes = vm.language.fileError;
                  vm.tableData2.push(arr);
                } else if (data[i].status == 4) { // 文件内容为空
                  arr.status = 'danger';
                  arr.mes = vm.language.fileWrongNull;
                  vm.tableData2.push(arr);
                } else if (data[i].status == 5) { // pdf未输入诱导文字
                  arr.status = 'danger';
                  arr.mes = vm.language.fileWrongPDF;
                  vm.tableData2.push(arr);
                } else if (data[i].status == 6) { // 不支持追踪标记
                  arr.status = 'danger';
                  arr.mes = vm.language.fileWrongTrack;
                  vm.tableData2.push(arr);
                } else if (data[i].status == 7) { // 不支持水印标记
                  arr.status = 'danger';
                  arr.mes = vm.language.fileWrongSWF;
                  vm.tableData2.push(arr);
                }
              }
              vm.$refs.upload.clearFiles();
              vm.subBtnDis = true;
            })
        }
      },
      // 表格行选择
      selectRow(selection) {
        let arr = new Array();
        for (let i in selection) {
          arr.push(selection[i].id);
        }
        this.selection = JSON.stringify(arr);
        console.log(selection);
      },
      // 下载文件
      handleDownFiles() {
        const vm = this;
        if (this.selection == '[]') {
          this.$message({
            message: vm.language.downFilesMes,
            type: 'warning'
          });
        } else {
          window.open(vm.$rootPath + '/wyfx/file/downLoadFile?fileIds=' + vm.selection);
        }
      },
      rowStyle() {
        return 'row-style'
      }
    },
    mounted() {
      const vm = this;
      // 獲取文本信息
      this.$httpi.get('/wyfx/page/getPageMsg', {
          params: {
            msg: 'FileMake'
          }
        })
        .then(function (response) {
          vm.language = response.data.data;
        })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .right-main {
    height: calc(100% - 20px);
    padding: 10px;
  }

  .outer {
    width: 100%;
    height: 100%;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }

  .inner {
    width: calc(100% - 40px);
    height: calc(100% - 40px);
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
  }

  .upload {
    width: 100%;
    max-width: 500px;
    height: calc(100% - 352px);
  }

  .upload-title {
    margin-top: 10px;
  }

  .fileMake {
    height: auto;
  }

  .makeform {
    margin-top: 10px;
  }

  .form-input {
    max-width: 300px;
  }

  .table {
    margin-top: 10px;
    height: calc((100% - 48px)/2);
  }

  .induced-tip {
    line-height: 14px;
  }

</style>

<style>
  .el-upload-list {
    height: calc(100% - 96px);
    overflow: auto;
    border-bottom: 1px solid rgb(223, 231, 236);
  }

  .row-style {
    display: none;
  }

</style>



