import Vue from 'vue'
import Router from 'vue-router'
import {httpi} from './http'
import Main from '@/pages/Main'
import error from '@/pages/Error'
import FileRecord from '@/pages/FileRecord'
import FileMake from '@/pages/FileMake'
import AlgorithmManage from '@/pages/AlgorithmManage'
import Monitor from '@/pages/Monitor'
import Detailed from '@/pages/Detailed'
import TrackAnalysis from '@/pages/TrackAnalysis'
import TrackDetail from '@/pages/TrackDetail'

Vue.use(Router)

const router = new Router({
  // html5history模式，返回真实路径需要后台配置
  // mode: 'history',
  routes: [{
      path: '/main',
      name: 'Main',
      component: Main,
      children: [{
          path: 'file-record',
          name: 'FileRecord',
          component: FileRecord
        },
        {
          path: 'file-make',
          name: 'FileMake',
          component: FileMake
        },
        {
          path: 'algorithm-manage',
          name: 'AlgorithmManage',
          component: AlgorithmManage
        },
        {
          path: 'monitor',
          name: 'Monitor',
          component: Monitor
        },
        {
          path: 'detailed',
          name: 'Detailed',
          component: Detailed
        },
        {
          path: 'track-analysis',
          name: 'TrackAnalysis',
          component: TrackAnalysis
        },
        {
          path: 'track-detail',
          name: 'TrackDetail',
          component: TrackDetail
        }
      ]
    },
    {
      path: '/error',
      name: 'Error',
      component: error
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.name == 'Error') {
    next();
  } else {
    httpi.get('/wyfx/function/verifyFunction', {
        params: {
          path: to.path
        }
      })
      .then(function (response) {
        if (response.data.code == '200') {
          next();
        } else if (response.data.code == '103') {
          httpi.get('/wyfx/page/redirect?path=sc')
            .then(function (response) {
              window.location.href = response.data.msg;
            });
        } else {
          next({
            name: 'Error'
          });
        }
      })
      .catch(function (error) {
        console.log(error);
        next({
          name: 'Error'
        });
      });
  }
})

export default router



