<template>
  <div class="right-main">
    <!-- 选择文件弹出框 -->
    <el-dialog :title="language.choseFile" :visible.sync="dialogVisible" size="small" top="5%">
      <el-table :data="gridData" stripe>
        <el-table-column prop="uuid" label="ID">
        </el-table-column>
        <el-table-column prop="fileName" :label="language.tabFileName">
        </el-table-column>
        <el-table-column width="80">
          <template scope="scope">
            <el-button size="small" @click="handleChose(scope.$index, scope.row)">{{language.chose}}</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination layout="sizes, prev, pager, next, total" :current-page="currentPage_grid" :page-size="pageSize_grid" :page-sizes="[10, 20, 50, 100, 200]"
          :total="totalElements_grid" @current-change="handleCurrentChange_grid" @size-change="handleSizeChange_grid"></el-pagination>
      </div>
    </el-dialog>
    <!-- 选择文件弹出框end -->
    <div class="outer">
      <div class="tool-bar">
        <el-button @click="handleShowFile">{{language.choseFile}}</el-button>
        <span :class="{hide:ishide}">
        <span class="tool-bar-txt">{{language.fileName}}</span>
        <span class="tool-bar-txt val">{{fileName}}</span>
        <span class="tool-bar-txt">{{language.fileId}}</span>
        <span class="tool-bar-txt val">{{fileUid}}</span>
        </span>
      </div>
      <div class="inner">
        <el-table :data="tableData" class="table" style="width: 100%;" height="100%" stripe>
          <el-table-column prop="ip" :label="language.tabIp">
          </el-table-column>
          <el-table-column :label="language.tabAddress">
            <template scope="scope">
              <span>{{scope.row.region}}{{scope.row.city}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="time" :label="language.tabTime">
          </el-table-column>
          <el-table-column prop="isp" :label="language.tabOperator">
          </el-table-column>
          <el-table-column prop="system" :label="language.tabSystem">
          </el-table-column>
          <el-table-column prop="device" :label="language.tabDevice">
          </el-table-column>
        </el-table>
        <div class="pagination">
          <el-pagination layout="sizes, prev, pager, next, total" :current-page="currentPage" :page-size="pageSize" :page-sizes="[20, 50, 100, 200, 400]"
            :total="totalElements" @current-change="handleCurrentChange" @size-change="handleSizeChange"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {
          choseFile: '',
          chose: '',
          tabFileName: '',
          fileName: '',
          fileId: '',
          tabIp: '',
          tabAddress: '',
          tabTime: '',
          tabOperator: '',
          tabSystem: '',
          tabDevice: ''
        },
        dialogVisible: false, // 弹出框的显示隐藏
        gridData: [], // 弹出框表格数据
        tableData: [],
        totalElements: 0, //总条目数
        currentPage: 1, // 当前页数
        pageSize: 20, // 每页显示条目数
        selection: [], // 当前选择得行
        currentPage_grid: 1,
        pageSize_grid: 10,
        totalElements_grid: 0,
        fileId: '',
        fileUid: '',
        fileName: '',
        ishide: true // 控制当前文件的显示和隐藏
      }
    },
    methods: {
      getLang(callBack) {
        const vm = this;
        // 獲取文本信息
        this.$httpi.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'TrackDetail'
            }
          })
          .then(function (response) {
            vm.language = response.data.data;
            callBack;
          })
      },
      // 选择文件弹出框
      handleShowFile() {
        this.dialogVisible = true;
        this.getGridData(1);
      },
      // 查询文件列表
      getGridData(pageNo) {
        const vm = this;
        this.$httpi.get('/wyfx/file/getAllFileSignByCondition', {
            params: {
              pageNo: pageNo,
              fileTypeId: null,
              startTime: null,
              endTime: null,
              pageSize: vm.pageSize_grid
            }
          })
          .then(function (response) {
            vm.gridData = response.data.data.content;
            vm.totalElements_grid = response.data.data.totalElements;
            vm.currentPage_grid = pageNo;
          })
      },
      // 查询数据
      getTableData(pageNo) {
        const vm = this;
        this.$httpi.get('/wyfx/fileData/getFileDataByFileId', {
            params: {
              pageNo: pageNo,
              fileId: vm.fileId,
              pageSize: vm.pageSize
            }
          })
          .then(function (response) {
            vm.tableData = response.data.data.content;
            vm.totalElements = response.data.data.totalElements;
            vm.currentPage = pageNo;
          })
      },
      handleCurrentChange_grid(currentPage) {
        this.getGridData(currentPage);
      },
      handleSizeChange_grid(pageSize) {
        this.pageSize_grid = pageSize;
        this.getGridData(1);
      },
      handleSizeChange(pageSize) {
        this.pageSize = pageSize;
        this.getTableData(1);
      },
      handleCurrentChange(currentPage) {
        this.getTableData(currentPage);
      },
      // 选择文件click
      handleChose(index, row) {
        this.fileId = row.id;
        this.fileUid = row.uuid;
        this.fileName = row.fileName;
        this.ishide = false;
        this.dialogVisible = false;
        this.getTableData(1);
      }
    },
    mounted() {
      this.getLang();
    }
  }

</script>


<style scoped>
  .right-main {
    height: calc(100% - 20px);
    padding: 10px;
  }

  .outer {
    width: 100%;
    height: 100%;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }

  .table {
    width: 100%;
    height: calc(100% - 22px);
  }

  .pagination {
    margin-top: 5px;
    text-align: center;
  }

  .tool-bar {
    width: calc(100% - 40px);
    height: 55px;
    line-height: 55px;
    background-color: #fff;
    padding: 0 20px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }

  .inner {
    margin-top: 2px;
    width: calc(100% - 40px);
    height: calc(100% - 97px);
    background-color: #fff;
    padding: 20px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }

  .tool-bar-txt {
    font-size: 14px;
    margin-left: 10px;
  }

  .tool-bar-txt.val {
    margin-left: 0;
    color: #38b3fc;
  }

  .hide {
    display: none;
  }

</style>



