<template>
  <div class="outer">
    <div class="right-main">
      <div class="inner">
        <div class="inner-con">
          <h3 class="title">{{language.fileRestore}}</h3>
          <div class="info clearfix">
            <div class="fl info-div">{{language.fileServerAddress}}
              <span></span>
            </div>
            <div class="fl info-div">{{language.fileExtraStatus}}
              <span class="sever-close"></span>
            </div>
            <div class="fl info-div">{{language.fileTime}}
              <span></span>
            </div>
          </div>
          <div class="info card">
            <div class="info-card clearfix">
              <div class="info-card-left fl">
                <div class="info-card-left-conent">
                  <div class="info-card-txt">{{language.totalFilesExtracted}}</div>
                  <div class="info-card-txt">{{language.getFiles}}</div>
                  <div class="info-number">{{fileRestore.count}}</div>
                </div>
              </div>
              <div class="info-card-right fl">
                <el-row class="info-card-right-row">
                  <el-col :span="8" class="info-card-right-col">
                    <div class="info-card-right-conent">
                      <el-progress type="circle" :percentage="fileRestore.httpPercent"></el-progress>
                      <div class="info-card-txt">{{language.fromHTTP}}</div>
                      <div class="info-card-txt num">{{fileRestore.httpCount}}</div>
                    </div>
                  </el-col>
                  <el-col :span="8" class="info-card-right-col">
                    <div class="info-card-right-conent">
                      <el-progress type="circle" :percentage="fileRestore.ftpPercent"></el-progress>
                      <div class="info-card-txt">{{language.fromFTP}}</div>
                      <div class="info-card-txt num">{{fileRestore.ftpCount}}</div>
                    </div>
                  </el-col>
                  <el-col :span="8" class="info-card-right-col">
                    <div class="info-card-right-conent">
                      <el-progress type="circle" :percentage="fileRestore.smtpPercent"></el-progress>
                      <div class="info-card-txt">{{language.fromSTMP}}</div>
                      <div class="info-card-txt num">{{fileRestore.smtpCount}}</div>
                    </div>
                  </el-col>
                </el-row>
              </div>
            </div>
          </div>
        </div>
        <div class="inner-con">
          <h3 class="title">{{language.filesAnalysis}}</h3>
          <div class="info clearfix">
            <div class="fl info-div">{{language.fileServerAddress}}
              <span></span>
            </div>
            <div class="fl info-div">{{language.fileExtraStatus}}
              <span class="sever-close"></span>
            </div>
            <div class="fl info-div">{{language.fileTime}}
              <span></span>
            </div>
          </div>
          <div class="info card">
            <div class="info-card clearfix">
              <div class="info-card-left fl">
                <div class="info-card-left-conent">
                  <div class="info-card-txt">{{language.filesAnalysisTxt1}}</div>
                  <div class="info-card-txt">{{language.filesAnalysisTxt2}}</div>
                  <div class="info-number">{{filesAnalysis.signCount}}</div>
                </div>
              </div>
              <div class="info-card-right fl">
                <el-row class="info-card-right-row">
                  <el-col :span="8" class="info-card-right-col">
                    <div class="info-card-right-conent">
                      <el-progress type="circle" :percentage="filesAnalysis.httpSignPercent"></el-progress>
                      <div class="info-card-txt">{{language.HTTPsend}}</div>
                      <div class="info-card-txt num">{{filesAnalysis.httpSignCount}}</div>
                    </div>
                  </el-col>
                  <el-col :span="8" class="info-card-right-col">
                    <div class="info-card-right-conent">
                      <el-progress type="circle" :percentage="filesAnalysis.ftpSignPercent"></el-progress>
                      <div class="info-card-txt">{{language.FTPsend}}</div>
                      <div class="info-card-txt num">{{filesAnalysis.ftpSignCount}}</div>
                    </div>
                  </el-col>
                  <el-col :span="8" class="info-card-right-col">
                    <div class="info-card-right-conent">
                      <el-progress type="circle" :percentage="filesAnalysis.smtpSignPercent"></el-progress>
                      <div class="info-card-txt">{{language.STMPsend}}</div>
                      <div class="info-card-txt num">{{filesAnalysis.smtpSignCount}}</div>
                    </div>
                  </el-col>
                </el-row>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {},
        fileRestore: {
          count: 0,
          ftpCount: 0,
          ftpPercent: 0,
          httpCount: 0,
          httpPercent: 0,
          smtpCount: 0,
          smtpPercent: 0
        },
        filesAnalysis: {
          ftpSignCount: 0,
          ftpSignPercent: 0,
          httpSignCount: 0,
          httpSignPercent: 0,
          signCount: 0,
          smtpSignCount: 0,
          smtpSignPercent: 0
        }
      }
    },
    mounted() {
      const vm = this;
      let loading = vm.$loading();
      vm.$http.all([vm.$http.get('/wyfx/fwFile/getFwFileCountAndPercent'),
          vm.$http.get('/wyfx/page/getPageMsg', {
            params: {
              msg: 'Monitor'
            }
          })
        ])
        .then(vm.$http.spread(function (data, language) {
          // 获取文本信息
          vm.language = language.data.data;
          // 获取数据
          vm.fileRestore = data.data.data.file;
          vm.filesAnalysis = data.data.data.signFile;
          loading.close();
        }));
    }
  }

</script>


<style scoped>
  .right-main {
    height: calc(100% - 20px);
    padding: 10px;
  }

  .outer {
    width: 100%;
    height: 100%;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }

  .inner-con {
    height: calc(50% - 40px);
    padding: 20px;
  }

  .info {
    margin-top: 10px;
  }

  .info.card {
    height: calc(100% - 70px);
  }

  .inner {
    width: 100%;
    height: 100%;
    background-color: #fff;
    border-radius: 5px;
    color: #687189;
  }

  .info-div {
    margin-right: 15px;
  }

  .sever-close {
    color: #d45757;
  }

  .sever-open {
    color: #25b07d;
  }

  .info-card {
    width: calc(60% - 2px);
    height: 100%;
    margin-right: 1%;
    border: 1px solid #d0d6e2;
    border-radius: 5px;
  }

  .info-card-left {
    width: 25%;
    height: 100%;
    border-right: 1px solid #d0d6e2;
    position: relative;
  }

  .info-number {
    font-size: 24px;
    color: #d57b81;
    text-align: center;
    margin-top: 5px;
  }

  .info-card-txt {
    margin-top: 5px;
    text-align: center;
  }

  .info-card-txt.num {
    font-weight: bold;
  }

  .info-card-left-conent {
    width: 100%;
    position: absolute;
    top: calc(50% - 47px);
    left: 0;
  }

  .info-card-right {
    width: calc(75% - 1px);
    height: 100%;
  }

  .info-card-right-row {
    height: 100%
  }

  .info-card-right-col {
    height: 100%;
    position: relative;
  }

  .info-card-right-conent {
    text-align: center;
    position: absolute;
    width: 100%;
    top: calc(50% - 90px);
    left: 0;
  }

</style>



